/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
$dark: #272635; 
$lightblue: #e8f3f7;
$breakpoint-mob: 480px;
$breakpoint-tab: 768px;
$breakpoint-desk: 1000px;

.flex-container {
    display: flex;
}
.flex-row {
    flex-direction: row;
}
.flex-col {
    flex-direction: column;
}

.grid-container {
    display: grid;
}

.grid-item {
    display: inline-grid;
}

.img {
    height: auto;
    width: 100%;
}

.blue {
    color: #1d8daf;
}

// Tablet
@media (min-width: $breakpoint-tab) {

    .img {
        height: auto;
        width: 50%;
    }
}

//Mobile
.nav-outer-wrapper {
    overflow: auto;
    z-index: 200;
}
.nav-outer {
    position: relative;
    top: 0;
    padding-top: 1em;
    width: 90%;
    margin: 0 auto;
    padding-bottom:1em;
    z-index: 30;
    background: #fff;
}
.nav-outer.active {
    width:100%!important;
}
.nav-outer.fixed-position {
    position: sticky;
    width: 100%!important;
}
.nav-social {
    display: inline-block;
    > li > a {
        font-size: 1.25em;
        margin-left: 1em;
        text-decoration: none;
        color: $dark;
        :hover {
            transform: scale(1.1);
        }
    }    
}
.nav-container {
    margin: 0.5em 0.5em 0.5em 0;
}
.nav-title {
    margin-left: 0.5em;
    font-size: 1.75em;
    > a {
        text-decoration: none;
        color: $dark;     
    }
}
.navbar ul {
    > li {
        list-style-type: none;
        margin-left: 1em;
        overflow: hidden;   
        > a {
            text-decoration: none;
            color: $dark;
            font-size: 1.1em;
            display: inline-block;
            padding: 1em;
            position: relative;
        }

    }
}
.navbar ul li a::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 2px;
    bottom: 0;
    left: 0;
    background-color: $dark;
    visibility: hidden;
    transform: scaleX(0);
    transition: all 0.3s ease-in-out 0s;
}
.navbar ul li a:hover::before {
    visibility: visible;
    transform: scaleX(1);
}
.navbar ul li a:hover {
    font-weight: 600;
}

#nav-button {
    display: inline-block;
    z-index: 25;
    margin-right: 2em;
    text-decoration:none;
    font-weight:600;
}
.off-canvas {
    display: none;
    opacity: 0;
    transition: opacity 1s;
}       
.off-canvas.active {
    display: block;
    opacity: 1;
    width: 100%;
    position: absolute;
    margin-top: 0;
    padding-top:1em;
    z-index: 2;
    color: $dark;
    background-color: rgba(255, 255, 255, 1);
    .navbar {
        background-color: rgba(255, 255, 255, 1);
        > ul {
            margin-bottom: 1em;
            > li {
                padding-top: 1em;
            }
        }
    }
    > * {
        color: $dark;
    }
    .nav-button {
        padding-top: 1em;
    }
    .nav-right {
        > * {
            display: inline-block;
            margin-left: 2em;
            text-decoration: none;
        }
        > .nav-social {
            margin-top: 1em;
            display: flex;
            flex-direction: row;
        }
        background-color: rgba(255, 255, 255, 1);
        box-shadow: 0 0 5px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
        padding-top: 1em;
        padding-bottom: 1em;
    }
}

// Tablet
@media (min-width: $breakpoint-tab) {
    #nav-button {
        display: none;
    }
    .off-canvas {
        display: flex;
        opacity: 1;
        width: 100%;
        position: relative;
        height: auto;
    }
    .nav-title {
        display: none;
    }   
    .navbar {
        height: auto;
        flex: 0 1 auto;
        padding: 0.25em;
        // justify-content: flex-start;
        > ul {
            display: flex;
            justify-content: space-around;
            flex-direction: row;
            li {
                margin-left: 0;
            }
        }
    }

    .nav-container {
        justify-content: space-between;
        > .navbar {
            height: auto;
        }
    } 
    
    .nav-right {
        flex: 1;
        width: 100%;
        margin-left:10%;
        justify-content: space-around;
        padding: 0;
        margin: 0;
        > * {
            display: flex;
            margin-left: auto;
        }
        > .btn {
            display: inline-flex;
            text-decoration: none;
        }
    }
    .nav-social {
        margin-left: 20%;
        margin-top: 0.75em;
    }
}


/**
 * Desktop
 */
@media (min-width: $breakpoint-desk) {

}

.btn {
    border-radius: 0.25em;
    border-width: 0.2em;
    font-size: 1.25em;
    padding: 0.5em 0.5em 1.5em 0.5em;
    height: 30px;
    padding-bottom: 2em;
    display: inline-block;
    cursor: pointer;
    text-decoration: none;
    text-align: center;
}
.btn:hover{
    box-shadow: 3px 2px 1px 0px rgba(0, 0, 0, 0.25);
}
.btn:active {
    background: #fff;
    transform: scale(0.99);
    box-shadow: 3px 2px 22px 1px rgba(78, 70, 70, 0.25);
}
.btn-dark {
    border: solid $dark;
    color: $dark; 
}
.btn-light {
    border: solid #fff;
    color: #fff;
}
.btn-animated {
    background-color: #272635;
    color:#fff;
    border:none;
    background-image:none;
    -webkit-box-shadow: none;
    -moz-box-shadow: none;
    box-shadow: none;
}

.btn-nav {
    border: solid #272635;
    color: #272635; 
    font-size: 1.15em;
    padding: 0.25em 0.4em 1.5em 0.4em;
    margin-left: auto;
    margin-top: 0.2em;
    border-radius:0.45em;
    > i {
        margin-top:0.1em;
    }
}

@media screen and (min-width: $breakpoint-tab) {
    .btn {
        display: inline-block;
        min-height: 46px;
        font-size: 1em;
        padding-bottom: 0.5em;
    }
}
h1 {
    font-size: 36px;
    line-height: 40px;
    display: block;
}

h2 {
    font-size: 30px;
    line-height: 40px;
}

h3 {
    font-size: 24px;
    line-height: 40px;
}

h4 {
    font-size: 18px;
    line-height: 20px;
}

h5 {
    font-size: 14px;
    line-height: 20px;
}

h6 {
    font-size: 12px;
    line-height: 20px;
}

body {
    font-family: 'Manrope', 'Nanum Gothic', sans-serif;
}

h1, h2, h3 {
    font-family: 'Arvo', serif;
}

p {
    font-weight: 500;
    line-height: 160%;
}
strong {
    font-weight: 550;
}
*, 
*:before, 
*:after {
    box-sizing: border-box;
}
body, html {
    height: 100%;

}

.site-wrapper {
    display: flex;
    flex-direction: column;
    margin: 0em 1em 0em 1em;
    > div.body-section {
        display: flex;
        flex-direction: column;
        flex: 1;
        width: 100%;
        > h1, h2, h3 {
            margin-top: 1em;
            margin-bottom: 1em;
        } 
        > .btn {
            margin-bottom: 1em;
        }
    }
    > .body-section {
        > p {
            margin-top: 1em;
            padding-bottom: 1em;
        }
        > * > .btn {
            margin-bottom: 1em;
        }
    }
}

.main-header {
    overflow: hidden;
    > img {
        border-top-right-radius: 1em;
        border-top-left-radius: 1em;
        background-size: cover;
        background-position: 50% 50%; 
        position: relative;
    }
    min-height: 60vw;
    width: 100%;
}

.hero-image {
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    border-top-left-radius: 1em;
    border-top-right-radius: 1em;

    > .hero-content {
        margin-left: 2em;
        text-align: left;
        top: 25%;
        display: flex;
        flex-direction: column;
        width: 90%;
        margin-top: 2em;
        flex-wrap: wrap;
        justify-content: space-around;
        padding-bottom: 2em;
        > a {
            max-width: 9em;
        }
        > * {
            display: block;
            color: #fff;
            margin-top: 3em;
            display: inline-block;
            flex: 1;
        }
    }
}
.swiper-container, .swiper-container-blue {
    width: 100%;
    max-width: 100%;
    max-height: 100vh;
    min-height: 0;
    min-width: 0;
    overflow: hidden;
}
.swiper-wrapper {
    max-height: 100%;
    height: 100%;
    display: flex;
}
.swiper-slide {
    width: auto;
    flex-shrink: 0;
    display: block;
    height: 100%;
    max-height: 100%;
    text-align: center;
    justify-content: center;
    img {
        width: 100%;
        max-width: 400px;
        height: auto;
    }
}
.swiper-container-blue {
    background-color: $lightblue;
    margin-left:0;
    margin-right:0;
    > .swiper-wrapper > .swiper-slide > img {
        width: 50%;
        max-width: 200px;
        height: auto;
    }
}

.block-image {
    object-fit: cover;
}

.body-section > .two-column-text {
    flex-direction: column;
    justify-content: space-between;
}
.body-section > .main,
.body-section > .secondary {
    margin-top: 1em;
}
.two-column-text {
    padding-bottom: 1em;
}
footer {
    background-color: $lightblue;
    padding-top: 1em;
    margin-top: 3em;
    > .body-section {
        margin: 1em 1em 1em 1em;
    }
}
.text-center {
    text-align: center;
}

input[type=text],
input[type=email],
input[type=phone],
textarea {
    width: 100%;
    padding: 0.75em 1em;
    margin: 0.5em 0;
    box-sizing: border-box;
    border: 0;
    font-family: 'Arvo', serif;
    :focus {
        border: 3px solid $dark;
    }
}

#contact-form {
    margin-top: 1em;
    padding: 1em 1em 1em 1em;
    .form-row {
        display: flex;
        flex-direction: column;
        padding: 0.25em;
        overflow: auto;
        > input {
            margin-top: 0.25em;
            width: 100%;
        }
      > .btn {
            width: auto; 
        }
    }
}
footer {
    .body-section {
        .contact-details {
            margin-top: 2em;
            li {
                margin-top: 1em;
            }
        }
    }
}

.errors {
    margin-top: 1em;
    font-weight: 550;
}
.error-field {
    border: 1px solid red;
}

// Tablet
@media (min-width: $breakpoint-tab) {
    .hero-image {
        .hero-content {
            width: 75%;
        }
    }

    .image-wrapper {
        .img {
            width: 100%;
        }
    }
}

// Desktop
@media (min-width: $breakpoint-desk) {

    .hero-image {
        .hero-content {
            width:60%;
        }
    }
    .site-wrapper {
        display: flex;
        flex-direction: column;
        margin: 0em 1em 0em 1em;
        margin-left: auto;
        > .body-section > * {
            margin: 0em 1em 0em 1em;
            margin-left: 2em;
        }
        > div.body-section {
            display: flex;
            flex-direction: row;
            margin-left: auto;
            > .main {
                width: 50%;
            }
            > h1, h2, h3 {
                margin-top: 1em;
                margin-bottom: 1em;
            } 
            > .btn {
                margin-bottom: 1em;
            }
            > .without-image > * {
                flex-direction:column!important;
            }
            > .full-width {
                min-width: 100%;
                flex-direction:column!important;
            }
        }
    }

    .body-section.without-image,
    .body-section.full-width {
            display: flex!important;
            flex-direction: column!important;
            flex: 1;
            width: 100%;
    }

    .main-header {
        overflow: hidden;
        margin-left: auto;
        margin-right: 1em;
        width: 98%;
        border-top-right-radius: 1em;
        border-top-left-radius: 1em;
        > img {
            background-size: cover;
            background-position: 50% 50%; 
            position: relative;
        }
        min-height: 35vw;
    }

    .image-wrapper {
        .image {
            width: 100%;
        }
    }
    //Animation for body elements
    .animate-slide {
        opacity: 0;
        transition: opacity ease-in-out 500ms;
    }
    .animate-slide.active-section {
        opacity: 1;
    }
    footer {
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
        .body-section {
            > *  {
                display: flex;
                flex: 1;
                width: 50%;
                flex-direction: column;
                flex-basis: 100%;
            }
        }
    }
}
$themeColor: #007aff !default;
$colors: (
  'white': #ffffff,
  'black': #000000,
) !default;


@font-face {
  font-family: 'swiper-icons';
  src: url('data:application/font-woff;charset=utf-8;base64, ')
    format('woff');
  font-weight: 400;
  font-style: normal;
}

:root {
  --swiper-theme-color: #{$themeColor};
}
.swiper-container {
  margin-left: auto;
  margin-right: auto;
  position: relative;
  overflow: hidden;
  list-style: none;
  padding: 0;
  /* Fix of Webkit flickering */
  z-index: 1;
}
.swiper-container-vertical > .swiper-wrapper {
  flex-direction: column;
}
.swiper-wrapper {
  position: relative;
  width: 100%;
  height: 100%;
  z-index: 1;
  display: flex;
  transition-property: transform;
  box-sizing: content-box;
}
.swiper-container-android .swiper-slide,
.swiper-wrapper {
  transform: translate3d(0px, 0, 0);
}
.swiper-container-multirow > .swiper-wrapper {
  flex-wrap: wrap;
}
.swiper-container-multirow-column > .swiper-wrapper {
  flex-wrap: wrap;
  flex-direction: column;
}
.swiper-container-free-mode > .swiper-wrapper {
  transition-timing-function: ease-out;
  margin: 0 auto;
}
.swiper-container-pointer-events {
  touch-action: pan-y;
  &.swiper-container-vertical {
    touch-action: pan-x;
  }
}
.swiper-slide {
  flex-shrink: 0;
  width: 100%;
  height: 100%;
  position: relative;
  transition-property: transform;
}
.swiper-slide-invisible-blank {
  visibility: hidden;
}
/* Auto Height */
.swiper-container-autoheight {
  &,
  .swiper-slide {
    height: auto;
  }

  .swiper-wrapper {
    align-items: flex-start;
    transition-property: transform, height;
  }
}

/* 3D Effects */
.swiper-container-3d {
  perspective: 1200px;
  .swiper-wrapper,
  .swiper-slide,
  .swiper-slide-shadow-left,
  .swiper-slide-shadow-right,
  .swiper-slide-shadow-top,
  .swiper-slide-shadow-bottom,
  .swiper-cube-shadow {
    transform-style: preserve-3d;
  }
  .swiper-slide-shadow-left,
  .swiper-slide-shadow-right,
  .swiper-slide-shadow-top,
  .swiper-slide-shadow-bottom {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    pointer-events: none;
    z-index: 10;
  }
  .swiper-slide-shadow-left {
    background-image: linear-gradient(to left, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
  }
  .swiper-slide-shadow-right {
    background-image: linear-gradient(to right, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
  }
  .swiper-slide-shadow-top {
    background-image: linear-gradient(to top, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
  }
  .swiper-slide-shadow-bottom {
    background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0));
  }
}

/* CSS Mode */
.swiper-container-css-mode {
  > .swiper-wrapper {
    overflow: auto;
    scrollbar-width: none; /* For Firefox */
    -ms-overflow-style: none; /* For Internet Explorer and Edge */
    &::-webkit-scrollbar {
      display: none;
    }
  }
  > .swiper-wrapper > .swiper-slide {
    scroll-snap-align: start start;
  }
}
.swiper-container-horizontal.swiper-container-css-mode {
  > .swiper-wrapper {
    scroll-snap-type: x mandatory;
  }
}
.swiper-container-vertical.swiper-container-css-mode {
  > .swiper-wrapper {
    scroll-snap-type: y mandatory;
  }
}



